import {
  AppBar,
  Icon,
  IconButton,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from '@material-ui/core'
import { Add, AttachMoney, CreditCard, History, Waves } from '@material-ui/icons'
import * as React from 'react'
import { IconMenu } from 'rice-instructor'
import { FilterDialog } from '@src/TransactionList/FilterDialog'

interface Prop {
  balance: string
  showForm: (formType: string) => void
  toggleTimeline: () => void
  onSearch: (filters: { isShared: boolean; categoryId: number | null; searchText: string }) => void
}

export const TransactionListAppBar = (props: Prop) => {
  const showNewTransactionForm = () => {
    props.showForm('transaction')
  }

  const showNewStatementForm = () => {
    props.showForm('statement')
  }

  const showWaveImportForm = () => {
    props.showForm('wave')
  }

  const toggleTimeline = () => {
    props.toggleTimeline()
  }

  return (
    <AppBar position="static" color="default">
      <Toolbar>
        <Tooltip title="Timeline">
          <IconButton onClick={toggleTimeline}>
            <History />
          </IconButton>
        </Tooltip>
        {props.balance ? (
          <Typography variant="body1" key="balance">{`Balance: $${props.balance}`}</Typography>
        ) : null}
        <div style={{ flex: 1 }} />
        <FilterDialog onSearch={props.onSearch} />
        <IconMenu icon={<Add />}>
          <MenuItem key="new-transaction-menu" onClick={showNewTransactionForm}>
            <ListItemIcon>
              <AttachMoney />
            </ListItemIcon>
            <ListItemText>Transaction</ListItemText>
          </MenuItem>
          <MenuItem key="new-statement-menu" onClick={showNewStatementForm}>
            <ListItemIcon>
              <CreditCard />
            </ListItemIcon>
            <ListItemText>Statement</ListItemText>
          </MenuItem>
          <MenuItem key="new-wave-menu" onClick={showWaveImportForm}>
            <ListItemIcon>
              <Waves color="primary" />
            </ListItemIcon>
            <ListItemText>Wave Import</ListItemText>
          </MenuItem>
        </IconMenu>
        <IconButton
          onClick={() => {
            sessionStorage.removeItem('authToken')
            window.location.href = '/'
          }}
        >
          <Icon className="material-icons">exit_to_app</Icon>
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}
