import * as React from 'react'
import { UrlStateContext } from '@src/Root/contexts'
import {
  DialogContentText,
  DialogTitle,
  Icon,
  IconButton,
  Button,
  Checkbox,
  FormControlLabel,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
} from '@material-ui/core'

interface Prop {
  onSearch: (filters: { isShared: boolean; categoryId: number | null; searchText: string }) => void
}

export const FilterDialog: React.FC<Prop> = (props) => {
  const urlFilters = React.useContext(UrlStateContext)
  const [open, setOpen] = React.useState(false)
  const [isShared, setIsShared] = React.useState(urlFilters.isShared || false)
  const [searchText, setSearchText] = React.useState<string>(urlFilters.searchText || '')
  const [categoryId, setCategoryId] = React.useState<number | null>(urlFilters.categoryId || null)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <IconButton onClick={handleClickOpen}>
        <Icon color="primary">filter_alt</Icon>
      </IconButton>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Filter Transactions</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Search transactions by category, text (merchant name and notes). By checking "Shared",
            you can limit the results to be shared transactions only.
          </DialogContentText>
          <TextField
            margin="dense"
            id="categoryId"
            label="Category"
            type="number"
            fullWidth
            value={categoryId || ''}
            onChange={(e) => setCategoryId(parseInt(e.target.value))}
          />
          <TextField
            onChange={(e) => setSearchText(e.target.value)}
            value={searchText || ''}
            margin="dense"
            id="searchText"
            label="Text Search"
            type="text"
            fullWidth
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={isShared}
                onChange={(e) => setIsShared(e.target.checked)}
                name="isShared"
              />
            }
            label="Shared"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            onClick={(e) => {
              props.onSearch({
                isShared,
                searchText,
                categoryId,
              })
              handleClose()
            }}
            color="primary"
          >
            Search
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
