import * as React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Slider from '@material-ui/core/Slider'
import { useApolloQuery } from '@src/queries/__generated__/UpdateTransaction.hooks'
import { DebouncedTextField } from '@src/components/DebouncedTextField'

const PrettoSlider = withStyles({
  root: {
    color: '#3f51b5',
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 4px)',
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider)

const getPercentage = (balance: number, total: number) => {
  return Number(((balance / total) * 100).toFixed(2))
}

export const BalanceAdjustField = (props: {
  transactionId
  total: number
  balanceAdjust: number
}) => {
  const [balanceAdjust, setBalanceAdjust] = React.useState<number>(props.balanceAdjust)
  const [percentage, setPercentage] = React.useState<number>(
    getPercentage(balanceAdjust, props.total),
  )
  const [updateTransaction, { loading, error }] = useApolloQuery({})

  const handleSliderChange = (event: any, newPercentage: number) => {
    const newBalanceAdjust = (newPercentage * props.total) / 100
    setPercentage(newPercentage)
    setBalanceAdjust(newBalanceAdjust)
    updateTransaction({
      variables: {
        id: props.transactionId,
        balanceAdjust: newBalanceAdjust.toString(),
      },
    })
  }
  if (error) return <div>{error.message}</div>

  return (
    <div>
      <Typography color="primary" id="input-slider" gutterBottom>
        Balance Adjust
      </Typography>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={9}>
          <PrettoSlider
            style={{ marginTop: '1.5rem' }}
            value={typeof percentage === 'number' ? percentage : 0}
            onChange={handleSliderChange}
            aria-labelledby="input-slider"
            defaultValue={0}
            min={-100}
            max={100}
            step={null}
            valueLabelDisplay="on"
            marks={[
              { value: -100, label: '-100%' },
              { value: -50, label: '-50%' },
              { value: 0, label: '0%' },
              { value: 50, label: '50%' },
              { value: 100, label: '100%' },
            ]}
            valueLabelFormat={(value: number) => `${value}%`}
          />
        </Grid>
        <Grid item xs={3}>
          <div style={{ paddingBottom: '2rem' }}>
            <DebouncedTextField
              type="number"
              loading={loading}
              value={balanceAdjust}
              setter={(newValue) => {
                setBalanceAdjust(newValue)
                setPercentage((Number(newValue) / props.total) * 100)
              }}
              onBounce={(newValue) => {
                updateTransaction({
                  variables: { id: props.transactionId, balanceAdjust: newValue },
                })
              }}
            />
          </div>
        </Grid>
      </Grid>
    </div>
  )
}
