import { Button } from '@material-ui/core'
import TextField from '@material-ui/core/TextField'
import * as React from 'react'

import { pushHistory } from '@src/history'
import { UrlPathEnum } from '@src/types'
import { BASE_URL } from '@src/actions'

export const LoginPage = (props) => {
  const [username, setUsername] = React.useState('')
  const [password, setPassword] = React.useState('')
  const [errorMessage, setErrorMessage] = React.useState(null)
  const submitLogin = () => {
    fetch(`${BASE_URL}/goTIOnaTErYeRsUraYhOMEO/login`, {
      method: 'POST',
      body: JSON.stringify({ username, password }),
      headers: {
        'Content-Type': 'application/json',
        'X-Requested-With': 'XMLHttpRequest',
      },
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.code === 200) {
          sessionStorage.setItem('authToken', data.token)
          // Redirect to root
          pushHistory(UrlPathEnum.Root)
        } else {
          console.error(data)
          setErrorMessage(data.message)
        }
      })
  }
  return (
    <div
      style={{
        height: `${window.innerHeight}px`,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
      }}
    >
      <TextField label="Username" onChange={(e) => setUsername(e.target.value)} />
      <br />
      <TextField
        error={!!errorMessage}
        helperText={errorMessage || ''}
        onChange={(e) => setPassword(e.target.value)}
        type="password"
        label="Password"
        onKeyPress={(e) => {
          if (e.charCode == 13) {
            submitLogin()
          }
        }}
      />
      <br />
      <Button variant="contained" color="primary" onClick={() => submitLogin()}>
        Login
      </Button>
    </div>
  )
}
