import * as React from 'react'
import { Tree } from './Tree'
import { getCategoryTree } from './selectors'
import { useApolloQuery } from '@src/queries/__generated__/GetCategories.hooks'

export const CategoryPage: React.FC<{}> = (props) => {
  const { data } = useApolloQuery()

  if (!data) {
    return <div>loading...</div>
  }
  const tree = getCategoryTree(data.categories)
  const root = {
    id: 0,
    name: 'root',
    mui_icon_id: null,
    parent_id: null,
    is_selectable: false,
    is_active: true,
    __typename: 'Category',
    description: 'root',
    children: Object.keys(tree).map((k) => tree[k]),
  }

  return (
    <Tree
      onSaveNode={(id: number, name: string) => console.log(id, name)}
      onSelectIcon={(id: number, iconId: string) => console.log(id, iconId)}
      node={root as any}
    />
  )
}
