import HighchartsData from 'highcharts-data'
import HighchartsDrilldown from 'highcharts-drilldown'
import * as React from 'react'
import Highchart from 'react-highcharts'
import u from 'updeep'
import { useApolloQuery } from '@src/queries/__generated__/GetProfitAndLossChart.hooks'
import { getIsoDateString } from '@src/utils'

HighchartsData(Highchart.Highcharts)
HighchartsDrilldown(Highchart.Highcharts)

const getChartConfig = ({ onClickColumn }) => {
  return {
    chart: {
      type: 'column',
      //height: 800,
      //width: 800
    },
    title: {
      text: 'Income/Expense Report',
    },
    xAxis: {
      type: 'category',
    },
    yAxis: {
      min: 0,
      title: {
        text: 'Spend',
        align: 'high',
      },
      labels: {
        overflow: 'justify',
      },
    },
    tooltip: {
      valueSuffix: ' dollars',
    },
    plotOptions: {
      bar: {
        dataLabels: {
          enabled: true,
        },
      },
      column: {
        dataLabels: {
          enabled: true,
        },
        allowPointSelect: false,
        events: {
          click: onClickColumn,
        },
      },
    },
    legend: {
      enabled: true,
      layout: 'vertical',
      align: 'right',
      verticalAlign: 'top',
      x: -40,
      y: 80,
      floating: true,
      borderWidth: 1,
      shadow: true,
    },
    credits: {
      enabled: false,
    },
  }
}
const today = new Date()
const sixMonthsAgo = new Date()
sixMonthsAgo.setDate(sixMonthsAgo.getDate() - 180)
// Set to start of the month
sixMonthsAgo.setDate(1)

export const PnlChart: React.FC<{}> = (props) => {
  const { loading, error, data } = useApolloQuery({
    variables: {
      startDate: getIsoDateString(sixMonthsAgo),
      endDate: getIsoDateString(today),
    },
  })
  if (loading) {
    return <div>loading ...</div>
  }
  if (error) return <div>error ...</div>

  const initial = getChartConfig({
    onClickColumn: (e) => console.log(e),
  })
  const titled = u(
    {
      title: {
        text: 'Profit & Loss Report',
      },
    },
    initial,
  )
  const config = Object.assign({}, titled, data!.profitAndLossChart)
  return (
    <div>
      <Highchart config={config} />
    </div>
  )
}
