import * as React from 'react'
import { Link, TypographyProps, Typography } from '@material-ui/core'

const isWide = window.innerWidth > 600

export const ResponsiveLabel = (props: TypographyProps) => {
  return <Typography variant={isWide ? 'body1' : 'caption'} {...props} />
}
interface ResponsiveLinkProps extends TypographyProps {
  disabled?: boolean
}

export const ResponsiveLink = (props: ResponsiveLinkProps) => {
  const { onClick, children, disabled, ...rest } = props
  return (
    <ResponsiveLabel {...rest}>
      {disabled === true ? children : <Link onClick={onClick}>{children}</Link>}
    </ResponsiveLabel>
  )
}
