import { Button, Dialog, DialogContent, DialogTitle, Icon } from '@material-ui/core'

import * as React from 'react'

import { Autocomplete } from 'rice-instructor'

interface DialogSelectProps {
  choices: Array<{
    value: string | number
    mui_icon_id?: string
    label: string
  }>
  onSelectOption: (option) => void
  value: string | null | number
}
const getInitialOption = (
  value: string | number | null,
  choices: Array<{
    value: string | number
    mui_icon_id?: string
    label: string
  }>,
) => {
  if (!value) return null
  const option = choices.find((x) => x.value === value)
  if (!option) {
    console.log(choices)
    throw new Error(`${value} not found `)
  }

  return option
}
const isWide = window.innerWidth > 600
export const DialogSelect = (props: DialogSelectProps) => {
  const { choices, value, onSelectOption } = props
  const [isOpen, setIsOpen] = React.useState(false)
  const initialOption = getInitialOption(value, choices)
  return (
    <div>
      <Button
        size={isWide ? 'large' : 'small'}
        onClick={() => setIsOpen(true)}
        variant="outlined"
        color="primary"
      >
        {initialOption ? initialOption.label : 'Select a Category'}
        {initialOption ? (
          <Icon style={{ marginRight: '5px' }}>{initialOption.mui_icon_id}</Icon>
        ) : null}
      </Button>
      <Dialog open={isOpen} fullWidth onClose={() => setIsOpen(false)}>
        <DialogTitle>Select a Category</DialogTitle>
        <DialogContent>
          <Autocomplete
            onSelectOption={(opt) => {
              onSelectOption(opt)
              setIsOpen(false)
            }}
            defaultValue={initialOption ? initialOption.value : undefined}
            placeholder="Select a category"
            options={choices}
          />
        </DialogContent>
      </Dialog>
    </div>
  )
}
