import { List, ListItem, ListItemText } from '@material-ui/core'
import { Route, Switch } from 'react-router'
import * as React from 'react'
import { hot } from 'react-hot-loader'
import { ApolloProvider } from '@apollo/react-hooks'

import { connect } from 'react-redux'
import { UrlStateContext } from './contexts'
import { CategoryPage } from '@src/CategoryPage'
import { UrlPathEnum } from '@src/types'
import { getPathName, pushHistory } from '@src/history'
import { LoginPage } from '@src/LoginPage'
import { PnlChart } from '@src/Report/PnlChart'
import { parseUrlState } from '@src/selectors'
import { TransactionList } from '@src/TransactionList'
import { client } from '@src/gqlClient'

interface StateProps {
  isAuthenticated: boolean
  urlState: any
}

type Prop = StateProps

const RootComponent: React.FC<Prop> = (props) => {
  return (
    <div style={{ maxWidth: '100%', flex: 8 }}>
      <UrlStateContext.Provider value={props.urlState}>
        <Body isAuthenticated={props.isAuthenticated} />
      </UrlStateContext.Provider>
    </div>
  )
}

export const Root = connect<StateProps, null>((state, ownProps) => {
  return {
    isAuthenticated: !!sessionStorage.getItem('authToken'),
    urlState: parseUrlState(),
    state,
  }
}, null)(RootComponent)

const PageList: React.FC = () => {
  const UrlPathsMap = {
    TransactionList,
    CategoryPage,
    PnlChart,
  }
  return (
    <List>
      {Object.keys(UrlPathsMap).map((c, index) => {
        return (
          <ListItem key={index} button onClick={() => pushHistory(c as UrlPathEnum)}>
            <ListItemText primary={c} />
          </ListItem>
        )
      })}
    </List>
  )
}

class Body extends React.Component<{ isAuthenticated: boolean }> {
  render() {
    if (getPathName() === UrlPathEnum.LoginPage) {
      return <LoginPage />
    }
    if (!this.props.isAuthenticated) {
      console.warn('not authenticated!!!')
      pushHistory(UrlPathEnum.LoginPage)
      return <div />
    }
    if (getPathName() === '/goTIOnaTErYeRsUraYhOMEO' || getPathName() === UrlPathEnum.Root) {
      return <PageList />
    }
    if (getPathName() === UrlPathEnum.TransactionList) {
      return <TransactionList />
    }
    if (getPathName() === UrlPathEnum.CategoryPage) {
      return <CategoryPage />
    }
    if (getPathName() === UrlPathEnum.PnlChart) {
      return <PnlChart />
    }

    throw new Error(`Invalid paths: ${getPathName()}`)
  }
}
export const App = hot(module)(() => (
  <ApolloProvider client={client}>
    <Switch>
      <Route path="/" component={Root} />
    </Switch>
  </ApolloProvider>
))
