import * as React from 'react'
import * as _ from 'lodash'
import { TextField } from '@material-ui/core'

interface IDebouncedTextField {
  helperText?: string
  onBounce: (newValue) => void
  setter: (newValue) => void
  type?: string
  value?: any
  loading?: boolean
  fullWidth?: boolean
  label?: string
}

export const DebouncedTextField: React.FunctionComponent<IDebouncedTextField> = ({
  helperText,
  setter,
  onBounce,
  label,
  type,
  value,
  loading,
  fullWidth,
}) => {
  const debounced = React.useCallback(_.debounce(onBounce, 1000), [])

  return (
    <TextField
      variant="outlined"
      error={!!loading}
      fullWidth={fullWidth}
      label={label}
      helperText={loading ? 'saving ...' : helperText}
      type={type}
      onChange={(event) => {
        setter(event.target.value)
        debounced(event.target.value)
      }}
      value={value}
    />
  )
}
