import * as React from 'react'

import {
  makeStyles,
  TextField,
  ListItemSecondaryAction,
  IconButton,
  Theme,
  createStyles,
  Icon,
  ListItemIcon,
  Collapse,
  List,
  ListItem,
  ListItemText,
} from '@material-ui/core'
import { ExpandMore, ExpandLess } from '@material-ui/icons'
import { Node } from './selectors'
import { IconSelect } from '@src/components/IconSelect'

interface ITreeProps {
  node: Node
  onSaveNode: (number, string) => void
  onSelectIcon: (id: number, iconId: string) => void
  level?: number
}
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    node: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
  }),
)

export const Tree: React.FC<ITreeProps> = (props) => {
  const { node, level = 0, onSelectIcon, onSaveNode } = props
  const classes = useStyles()

  const [open, setOpen] = React.useState(true)

  const handleClick = () => {
    setOpen(!open)
  }
  return (
    <List component="nav" className={classes.node}>
      <ListItem button onClick={handleClick} style={{ paddingLeft: `${32 * level}px` }}>
        <ListItemIcon>
          <Icon color="secondary">{node.mui_icon_id}</Icon>
        </ListItemIcon>
        <ListItemText primary={node.name}></ListItemText>
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {node.children.map((child) =>
            child.children.length ? (
              <Tree
                onSelectIcon={onSelectIcon}
                onSaveNode={onSaveNode}
                key={child.id}
                node={child}
                level={level + 1}
              />
            ) : (
              <NodeListItem
                onSelectIcon={onSelectIcon}
                onSaveNode={onSaveNode}
                key={child.id}
                node={child}
                level={level + 1}
              />
            ),
          )}
        </List>
      </Collapse>
    </List>
  )
}

const NodeListItem: React.FC<{
  node: Node
  level: number
  onSaveNode: (number, string) => void
  onSelectIcon: (id: number, iconId: string) => void
}> = ({ node, level, onSaveNode, onSelectIcon }) => {
  const [editable, setEditable] = React.useState(false)
  const [pickingIcon, setPickingIcon] = React.useState(false)
  const [nodeName, setNodeName] = React.useState(node.name)
  const [iconId, setIconId] = React.useState(node.mui_icon_id)
  return (
    <>
      <ListItem
        key={node.id}
        dense
        button
        role={undefined}
        style={{ paddingLeft: `${32 * (level + 1)}px` }}
      >
        {editable ? (
          <TextField
            autoFocus={true}
            value={nodeName}
            onChange={(event) => setNodeName(event.target.value)}
            onBlur={() => {
              setEditable(false)
              onSaveNode(node.id, nodeName)
            }}
          />
        ) : (
          <>
            {pickingIcon ? (
              <IconSelect
                onChange={(e, child) => {
                  onSelectIcon(node.id, e.target.value)
                  setIconId(e.target.value)
                  setPickingIcon(false)
                }}
                initialValue={iconId}
              ></IconSelect>
            ) : (
              <ListItemText onClick={() => setEditable(true)} primary={nodeName}></ListItemText>
            )}
            <ListItemSecondaryAction>
              <IconButton onClick={() => setPickingIcon(true)} edge="end">
                <Icon color="primary">{iconId}</Icon>
              </IconButton>
            </ListItemSecondaryAction>
          </>
        )}
      </ListItem>
    </>
  )
}
