import { IImportBatch, ICheckPoint, IEvent } from './types'
import { EventType } from '@src/types/__generated__/globalTypes'

export const getEventDescription = (e: IEvent) => {
  if (e.eventType === EventType.CHECKPOINT) {
    const checkpoint = e as ICheckPoint
    return checkpoint.signedTs
      ? `balance: ${checkpoint.signedBalance}  Signed at ${new Date(
          checkpoint.signedTs,
        ).toLocaleDateString()}`
      : 'Not Signed'
  }
  const batch: IImportBatch = e as IImportBatch
  if (batch.isStatement) {
    return `${batch.origin}: ${batch.startDate} TO ${batch.endDate}`
  }
  // Imported from wave or plaid
  if (e.eventType === EventType.BATCH_IMPORT) {
    const batch_import = e as IImportBatch
    return `${batch_import.origin}: ${batch_import.startDate.slice(
      0,
      10,
    )} TO ${batch_import.endDate.slice(0, 10)}`
  }

  throw new Error('not possible')
}
export const getEventIcon = (event: IEvent) => {
  if (event.eventType === EventType.CHECKPOINT) {
    return 'toll'
  }
  const batch: IImportBatch = event as IImportBatch
  if (batch.isStatement) {
    return 'cloud_upload'
  }
  if (batch.eventType === EventType.BATCH_IMPORT) {
    const batch_import = batch
    return { plaid: 'grid_on', wave: 'waves' }[batch_import.origin]
  }
  throw new Error('not possible')
}

export const getEventButtonIcon = (event: IEvent) => {
  if (event.eventType === EventType.CHECKPOINT) {
    return 'plus_one'
  }
  return null
}
