import {
  Button,
  Divider,
  ExpansionPanel,
  ExpansionPanelActions,
  ExpansionPanelSummary,
  Grid,
  TextField,
} from '@material-ui/core'
import * as React from 'react'
import {
  QUERY as GET_TRANSACTIONS_QUERY,
  GetTransactions,
  GetTransactions_categories as ICategory,
} from '@src/queries/__generated__/GetTransactions.hooks'
import { DialogSelect } from '@src/components/DialogSelect'
import { useApolloQuery } from '@src/queries/__generated__/CreateTransaction.hooks'
import { CreateTransactionInput } from '@src/types/__generated__/globalTypes'

import { getToday } from '@src/utils'

interface NewTransactionFormProps {
  onClose: () => void
  selectableCategories: Array<ICategory>
}

type FieldName =
  | 'issuedDate'
  | 'merchantName'
  | 'notes'
  | 'categoryId'
  | 'totalAmount'
  | 'balanceAdjust'

const fieldConfig = {
  issuedDate: { type: 'date', required: true },
  merchantName: { type: 'text', required: true },
  notes: { type: 'text', required: false },
  categoryId: { type: 'number', required: false },
  balanceAdjust: { type: 'number', required: false },
  totalAmount: { type: 'number', required: true },
}

const FieldInput = (props: {
  fieldName: FieldName
  updateField: (fieldName) => (event) => void
  instance: CreateTransactionInput
}) => {
  const { fieldName, updateField, instance } = props
  const config = fieldConfig[fieldName]
  let error = false
  if (config.required && !instance[fieldName]) {
    error = true
  } else if (config.required && config.type === 'number' && !instance[fieldName]) {
    error = true
  }

  return (
    <TextField
      type={config.type}
      required={config.required}
      error={error}
      label={fieldName}
      value={instance[fieldName]}
      onChange={updateField(fieldName)}
    />
  )
}
const initialForm: CreateTransactionInput = {
  issuedDate: getToday(),
  merchantName: '',
  notes: '',
  categoryId: null,
  totalAmount: '0',
  balanceAdjust: '0',
}

export const NewTransactionForm: React.FC<NewTransactionFormProps> = (props) => {
  const { selectableCategories, onClose } = props
  const [formData, setFormData] = React.useState<CreateTransactionInput>(initialForm)
  const [createTransaction, { error, loading }] = useApolloQuery({
    update(cache, { data }) {
      const variables = {
        // TODO: send real offset and limit
        filters: { offset: 0, limit: 40 },
      }
      if (data?.createTransaction?.isOk) {
        const cached = cache.readQuery<GetTransactions>({
          query: GET_TRANSACTIONS_QUERY,
          variables,
        })
        cache.writeQuery({
          query: GET_TRANSACTIONS_QUERY,
          data: Object.assign({}, cached, {
            transactions: [data.createTransaction.transaction, ...cached!.transactions],
          }),
          variables,
        })
      }
    },
  })

  if (error) {
    return <div>{error.message}</div>
  }

  const createNewTransaction = () => {
    createTransaction({
      variables: {
        input: {
          issuedDate: formData.issuedDate,
          totalAmount: formData.totalAmount,
          merchantName: formData.merchantName,
          balanceAdjust: formData.balanceAdjust || '0',
          categoryId: formData.categoryId,
          notes: formData.notes,
        },
      },
    })
    onClose()
  }
  const updateField = (fieldName: FieldName) => {
    const old = formData
    return (event) => {
      const newState = Object.assign({}, old, {
        [fieldName]: event.target.value,
      })
      setFormData(newState)
    }
  }
  const isValid = () => {
    const t = formData
    return [t.issuedDate, t.merchantName, +t.totalAmount].every(Boolean)
  }
  const transaction = formData
  return (
    <div style={{ paddingTop: '10px' }}>
      <ExpansionPanel expanded={true}>
        <ExpansionPanelSummary>
          <Grid container spacing={10}>
            <Grid item xs={6} sm={4}>
              <FieldInput instance={transaction} fieldName="issuedDate" updateField={updateField} />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput
                fieldName="merchantName"
                instance={transaction}
                updateField={updateField}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput
                fieldName="totalAmount"
                instance={transaction}
                updateField={updateField}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <DialogSelect
                value={transaction.categoryId || ''}
                onSelectOption={(option) => {
                  const newState = Object.assign({}, formData, {
                    categoryId: option.value,
                  })
                  setFormData(newState)
                }}
                choices={selectableCategories.map((cat) => {
                  return {
                    value: cat.id,
                    label: cat.description,
                    mui_icon_id: cat.mui_icon_id || '',
                  }
                })}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput fieldName="notes" instance={transaction} updateField={updateField} />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput
                fieldName="balanceAdjust"
                instance={transaction}
                updateField={updateField}
              />
            </Grid>
          </Grid>
        </ExpansionPanelSummary>
        <Divider />
        <ExpansionPanelActions>
          <Button
            size="small"
            onClick={() => {
              setFormData(initialForm)
              onClose()
            }}
          >
            Cancel
          </Button>
          <Button size="small" color="primary" onClick={createNewTransaction} disabled={!isValid()}>
            {loading ? `saving ...` : `Save`}
          </Button>
        </ExpansionPanelActions>
      </ExpansionPanel>
    </div>
  )
}
